@import "reset";
@import "vars";
@import "mixins";
@import "extends";

html {
  scroll-behavior: smooth; }

body {
  font-family: $title-font;
  position: relative; }

a {
  transition: color 0.5s, background-color 0.5s, outline-color 0.5s, border 0.5s; }

.container {
  width: $grid-width;
